* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #8190e9;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  color: #fff;
  background: linear-gradient(45deg, #ff6b6b, #c44569);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 20px;
  font-family: Arial, sans-serif;
  font-size: 24px;
  display: flex;
}

.field {
  text-align: center;
}

.value {
  width: 60px;
  display: inline-block;
}

.label {
  color: #fff;
  text-transform: uppercase;
  margin-top: 5px;
  font-size: 16px;
}

.datetimer {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 10px;
  display: flex;
}

.datetimer button[data-start] {
  color: #e6fa0a;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-family: Arial, sans-serif;
  font-size: 18px;
  position: relative;
  overflow: hidden;
}

.datetimer input[type="text"] {
  color: #e6fa0a;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-family: Arial, sans-serif;
  font-size: 18px;
  position: relative;
  overflow: hidden;
}

.form {
  background-color: #f2f2f2;
  border-radius: 5px;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.form label {
  margin-bottom: 10px;
  font-weight: bold;
  display: block;
}

.form button {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  font-size: 16px;
  display: block;
}

.form input[type="number"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  font-size: 16px;
  display: block;
}

.form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
}

.form button:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

